<template>
  <div id="article-add">
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <Input v-model="articleData.title" placeholder=""/>
        </div>
      </div>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <Input v-model="articleData.preface" type="textarea" :rows="4" placeholder=""/>
        </div>
      </div>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <label for="uploadImg" class="cdl-button blue">
            <Icon type="md-cloud-upload" :size="18"/> 
          </label>
          <input
            type="file"
            accept="image/gif, image/jpeg, image/jpg, image/png, image/svg"
            id="uploadImg"
            @change="uploadImg"
            :value="fileVal"
          >
        </div>
      </div>
      <div id="cover" v-show="articleData.cover">
        <i class="uploadImgClose cdl-close fa fa-close" title="" @click="closeUploadImg"></i>
        <img :src="articleData.cover" alt="">
      </div>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <Select v-model="articleData.tag_id">
            <Option v-for="item in tagList" :value="item.tid" :key="item.tid">{{ item.tag_name }}</Option>
          </Select>
        </div>
      </div>
    </div>
    <div class="cdl-form-item">
      <mavon-editor
        ref="md"
        :value="articleData.markdownText"
        @imgAdd="imgAdd"
        :ishljs="true"
        @change="articleSave"
      ></mavon-editor>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <Button type="primary" :size="'large'" @click="send(1)" style="margin-right: 20px;">
          <Icon type="ios-send"/>
        </Button>
        <Button type="warning" :size="'large'" @click="send(0)">
          <Icon type="md-book"/>
        </Button>
      </div>
    </div>
  </div>
</template>
<script>
import {
  addArticle,
  editArticle,
  tagList as tagListReq,
  uploadImg
} from "@/server/server";
export default {
  props: {
    type: {
      type: String
    },
    articleData: {
      type: Object,
      default() {
        return {
          title: "", // 
          preface: "", // 
          cover: "", // 
          tag_id: "", // 
          markdownText: "", // 
          markdownHtml: "", // 
          state: 1
        };
      }
    }
  },
  data() {
    return {
      fileVal: "", // input[type="file"] 
      tagList: [] // 
    };
  },
  created() {
    // 
    tagListReq().then(({ d: tagList }) => {
      this.tagList = tagList;
      if (this.type === "add") this.articleData.tag_id = tagList[0].tid;
    });
  },
  methods: {
    // 
    uploadImg(e) {
      const loading = new c.Loading("~");
      const file = e.target.files[0];

      const formdata = new FormData();
      formdata.append("image", file);

      uploadImg(formdata)
        .then(({ d: data }) => {
          this.articleData.cover = data.src;
        })
        .finally(() => {
          loading.close();
        });
    },
    // 
    closeUploadImg() {
      this.articleData.cover = "";
      // input file
      this.fileVal = "";
    },
    // 
    articleSave(markdownText, markdownHtml) {
      this.articleData.markdownText = markdownText;
      this.articleData.markdownHtml = markdownHtml;
    },
    // markdown 
    imgAdd(pos, file) {
      const $mavon = this.$refs.md;
      const loading = new c.Loading("~");
      // 
      const formdata = new FormData();
      formdata.append("image", file);
      uploadImg(formdata)
        .then(data => {
          $mavon.$img2Url(pos, data.src);
        })
        .finally(() => {
          loading.close();
        });
    },
    // 
    send(state) {
      // loading
      const loading = new c.Loading(``);
      this.articleData.state = state;

      // 
      let reqFn = null;
      if (this.type === "add") reqFn = addArticle;
      if (this.type === "edit") reqFn = editArticle;
      reqFn(this.articleData)
        .then(data => {
          if (data.c === 0)
            this.$router.push({
              name: state
                ? "article/articleMainList"
                : "article/articleDraftList"
            });
        })
        .finally(() => {
          loading.close();
        });
    }
  }
};
</script>
<style lang="less">
.markdown-body img {
  width: 300px !important;
}
</style>


