<template>
  <div id="article-list">
    <div class="search-box">
      <Input
        search
        enter-button=""
        autofocus
        placeholder="id///"
        @on-search="articleSearch"
      />
    </div>
    <c-list
      ref="cList"
      :primaryID="'aid'"
      :columns="columns"
      :list="articleList"
      :total="total"
      :btns="btns"
      :pageSize="pageSize"
      :batchDelText="batchDelText"
      @pageChange="pageChange"
      @batchDel="batchDelArticle"
    ></c-list>
  </div>
</template>

<script>
import cList from "@/components/c-list";
import {
  articleList as articleListReq,
  getArticleBySearch
} from "@/server/server";
export default {
  props: {
    state: {
      type: Number,
      default: 1
    },
    handle: {
      type: Object,
      default() {
        return {
          title: "",
          width: 180,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small",
                    icon: "ios-brush"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.update_article(params.row.aid);
                    }
                  }
                },
                ""
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small",
                    icon: "md-trash"
                  },
                  on: {
                    click: () => {
                      this.delSingleArticle(params.row.aid);
                    }
                  }
                },
                ""
              )
            ]);
          }
        };
      }
    },
    batchDelText: {
      type: String
    },
    btns: {
      type: Array
    }
  },
  components: {
    cList
  },
  created() {
    this.loadArticleList(1);
    this.columns.push(this.handle);
  },
  data() {
    return {
      columns: [
        {
          type: "selection",
          width: 60,
          align: "center"
        },
        {
          type: "index",
          width: 60,
          align: "center"
        },
        {
          title: "ID",
          key: "aid"
        },
        {
          title: "",
          key: "title"
        },
        {
          title: "",
          key: "date"
        },
        {
          title: "",
          key: "tag_name"
        }
      ],
      articleList: [],
      total: 0,
      pageSize: 0
    };
  },
  methods: {
    // 
    update_article(aid) {
      this.$router.push({ name: "articleEdit", params: { aid } });
    },
    // 
    pageChange(page) {
      this.loadArticleList(page);
    },
    // 
    loadArticleList(page) {
      articleListReq(page, this.state).then(({ d: data }) => {
        this.articleList = data.articleList;
        this.total = data.total;
        this.pageSize = data.pageSize;
      });
    },
    // 
    articleSearch(searchValue) {
      getArticleBySearch(searchValue, 1, this.state).then(({ d: data }) => {
        this.articleList = data.articleList;
        this.total = data.total;
        this.pageSize = data.pageSize;
      });
    },
    delSingleArticle(aid) {
      this.$Modal.confirm({
        title: "",
        content: "<p></p>",
        onOk: () => {
          this.batchDelArticle([aid]);
        }
      });
    },
    // 
    batchDelArticle(aids) {
      this.$emit("batchDelArticle", aids);
    }
  }
};
</script>

<style>
.search-box {
  margin-bottom: 10px;
}
</style>


