<template>
  <div id="article-add">
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <Input v-model="tagName" placeholder=""/>
        </div>
      </div>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <label for="uploadImg" class="cdl-button blue">
            <Icon type="md-cloud-upload" :size="18"/>
          </label>
          <input
            type="file"
            accept="image/gif, image/jpeg, image/jpg, image/png, image/svg"
            id="uploadImg"
            @change="uploadImg"
            :value="tagImg"
          >
        </div>
      </div>
      <div id="cover" v-show="tagImgSrc">
        <i class="uploadImgClose cdl-close fa fa-close" title="" @click="closeUploadImg"></i>
        <img :src="tagImgSrc" alt="">
      </div>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <Button
          type="primary"
          :size="'large'"
          @click="send"
          :disabled="btnLoading"
          :loading="btnLoading"
        ></Button>
      </div>
    </div>
  </div>
</template>
<script>
import { uploadTagImg, addTag, getTagByTid } from "@/server/server";
export default {
  data() {
    return {
      tagName: "",
      tagImg: "",
      tagImgSrc: "",
      btnLoading: false
    };
  },
  methods: {
    // 
    uploadImg(e) {
      const loading = new c.Loading("~");
      const file = e.target.files[0];

      const formdata = new FormData();
      formdata.append("image", file);

      uploadTagImg(formdata)
        .then(data => {
          this.tagImgSrc = data.d.src;
        })
        .finally(() => {
          loading.close();
        });
    },
    closeUploadImg() {
      this.tagImgSrc = "";
      // input file
      this.tagImg = "";
    },
    // 
    send() {
      this.btnLoading = true;
      addTag({ tagName: this.tagName, tagImgSrc: this.tagImgSrc })
        .then(data => {
          if (data.c === 0) this.$router.push({ path: "tagList" });
        })
        .finally(() => {
          this.btnLoading = false;
        });
    }
  }
};
</script>
<style lang="less">
</style>


