<template>
  <c-list :primaryID="'tid'" :columns="columns" :list="tagList" @batchDel="batchDelTag"></c-list>
</template>

<script>
import cList from "@/components/c-list";
import { tagList as tagListReq, tagDel } from "@/server/server";
export default {
  components: {
    cList
  },
  created() {
    this.loadTag();
  },
  data() {
    return {
      columns: [
        {
          type: "selection",
          width: 60,
          align: "center"
        },
        {
          title: "ID",
          key: "tid"
        },
        {
          title: "",
          key: "tag_name"
        },
        {
          title: "",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.tagDetail(params.row.tid);
                    }
                  }
                },
                ""
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.singleDelTag(params.row.tid);
                    }
                  }
                },
                ""
              )
            ]);
          }
        }
      ],
      tagList: []
    };
  },
  methods: {
    // 
    tagDetail(tid) {
      this.$router.push({ path: `tagUpd/${tid}` });
    },
    // 
    loadTag() {
      tagListReq().then(({ d: tagList }) => {
        this.tagList = tagList;
      });
    },
    // 
    singleDelTag(tid) {
      this.$Modal.confirm({
        title: "",
        content: "<p></p>",
        onOk: () => {
          this.batchDelTag([tid]);
        }
      });
    },
    // 
    batchDelTag(tids) {
      if (!tids || !tids.length) {
        this.$Message.warning("");
        return;
      }
      tagDel(tids).then(data => {
        if (data.c === 0) this.loadTag();
      });
    }
  }
};
</script>

<style lang="css">
</style>


