<template>
  <div id="article-add">
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <Input v-model="newTagName" placeholder=""/>
        </div>
      </div>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <span class="cdl-form-title"></span>
        <div class="cdl-form-cnt">
          <label for="uploadImg" class="cdl-button blue">
            <Icon type="md-cloud-upload" :size="18"/> 
          </label>
          <input
            type="file"
            accept="image/gif, image/jpeg, image/jpg, image/png, image/svg"
            id="uploadImg"
            @change="uploadImg"
            :value="tagImg"
          >
        </div>
      </div>
      <div id="cover" v-show="newTagImgSrc">
        <i class="uploadImgClose cdl-close fa fa-close" title="" @click="closeUploadImg"></i>
        <img :src="newTagImgSrc" alt="">
      </div>
    </div>
    <div class="cdl-form-item">
      <div class="cdl-form-wrap">
        <button type="button" class="cdl-button blue" @click="send"></button>
      </div>
    </div>
  </div>
</template>
<script>
import { uploadTagImg, updTag, getTagByTid } from "@/server/server";
export default {
  data() {
    return {
      tid: "",
      oldTagName: "",
      newTagName: "",
      tagImg: "",
      newTagImgSrc: "",
      oldTagImgSrc: ""
    };
  },
  methods: {
    // 
    uploadImg(e) {
      const loading = new c.Loading("~");
      const file = e.target.files[0];

      const formdata = new FormData();
      formdata.append("image", file);

      uploadTagImg(formdata)
        .then(data => {
          this.newTagImgSrc = data.d.src;
        })
        .finally(() => {
          loading.close();
        });
    },
    closeUploadImg() {
      this.newTagImgSrc = "";
      // input file
      this.tagImg = "";
    },
    // 
    send() {
      updTag({
        tid: this.tid,
        oldTagName: this.oldTagName,
        newTagName: this.newTagName,
        newTagImgSrc: this.newTagImgSrc,
        oldTagImgSrc: this.oldTagImgSrc
      }).then(data => {
        if (data.c === 0) this.$router.push({ name: "classificationList" });
      });
    }
  },
  created() {
    // tid
    const { tid } = this.$route.params;
    if (tid) {
      // aid
      getTagByTid(tid).then(({ d }) => {
        this.tid = d.tagInfo.tid;
        this.oldTagName = d.tagInfo.tag_name;
        this.newTagName = d.tagInfo.tag_name;
        this.newTagImgSrc = d.tagImgSrc;
        this.oldTagImgSrc = d.tagImgSrc;
      });
    }
  }
};
</script>
<style lang="less">


