<template>
  <article-list
    :handle="handle"
    :state="-1"
    batchDelText=""
    :btns="btns"
    ref="articleBaseList"
    @batchDelArticle="batchDelArticle"
  ></article-list>
</template>

<script>
import articleList from "./article.base.list";
import { articleDel, articleRecovery } from "@/server/server";
export default {
  components: {
    articleList
  },
  data() {
    return {
      handle: {
        title: "",
        width: 220,
        align: "center",
        render: (h, params) => {
          return h("div", [
            h(
              "Button",
              {
                props: {
                  type: "primary",
                  size: "small"
                },
                style: {
                  marginRight: "10px"
                },
                on: {
                  click: () => {
                    this.singleRecoveryArticle(params.row.aid);
                  }
                }
              },
              ""
            ),
            h(
              "Button",
              {
                props: {
                  type: "error",
                  size: "small"
                },
                on: {
                  click: () => {
                    this.singleDelArticle(params.row.aid);
                  }
                }
              },
              ""
            )
          ]);
        }
      },
      btns: [
        {
          id: 1,
          type: "success",
          fn: aids => {
            this.batchRecoveryArticle(aids);
          },
          icon: "md-return-left",
          text: ""
        }
      ]
    };
  },
  methods: {
    // 
    singleDelArticle(aid) {
      this.$Modal.confirm({
        title: "",
        content: "<p></p>",
        onOk: () => {
          this.batchDelArticle([aid]);
        }
      });
    },
    // 
    batchDelArticle(aids) {
      articleDel(aids).then(data => {
        if (data.c === 0) this.$refs.articleBaseList.loadArticleList(1);
      });
    },
    // 
    singleRecoveryArticle(aid) {
      this.batchRecoveryArticle([aid]);
    },
    // 
    batchRecoveryArticle(aids) {
      articleRecovery(aids).then(data => {
        if (data.c === 0) this.$refs.articleBaseList.loadArticleList(1);
      });
    }
  }
};
</script>


