<template>
  <div id="message-list">
    <c-list
      primaryID="mid"
      :columns="columns"
      :list="messageList"
      :total="total"
      :pageSize="pageSize"
      @pageChange="pageChange"
      @batchDel="batchDelMessage"
    ></c-list>
    <Modal
      v-model="replyModal"
      footer-hide
      :styles="{position: 'absolute', width: '80%', height: '80%', left: '10%', top: '10%'}"
      title=""
      @on-cancel="cancel"
    >
      <reply-list :mid="nowMid"></reply-list>
    </Modal>
  </div>
</template>

<script>
import cList from "@/components/c-list";
import replyList from "./reply-list";
import { getMessageList, getMessageCount, messageDel } from "@/server/server";
export default {
  components: {
    cList,
    replyList
  },
  created() {
    this.loadMessageList(1);
    this.loadMessageCount();
  },
  data() {
    return {
      columns: [
        {
          type: "selection",
          width: 60,
          align: "center"
        },
        {
          type: "index",
          width: 60,
          align: "center"
        },
        {
          title: "",
          key: "content"
        },
        {
          title: "",
          key: "user"
        },
        {
          title: "",
          key: "likeCount"
        },
        {
          title: "ip",
          key: "uip"
        },
        {
          title: "",
          key: "city"
        },
        {
          title: "",
          key: "date"
        },
        {
          title: "",
          width: 180,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h(
                "Button",
                {
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  style: {
                    marginRight: "5px"
                  },
                  on: {
                    click: () => {
                      this.replyShow(params.row.mid);
                    }
                  }
                },
                ""
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click: () => {
                      this.singleDelMessage(params.row.mid);
                    }
                  }
                },
                ""
              )
            ]);
          }
        }
      ],
      messageList: [],
      total: 0,
      pageSize: 0,
      nowMid: "",
      replyModal: false
    };
  },
  methods: {
    // 
    pageChange(page) {
      this.loadMessageList(page);
    },
    // 
    loadMessageList(page) {
      getMessageList(page).then(({ d }) => {
        this.messageList = d.messageList;
        this.pageSize = d.pageSize;
      });
    },
    loadMessageCount() {
      getMessageCount().then(({ d }) => {
        this.total = d.total;
      });
    },
    // 
    replyShow(mid) {
      this.nowMid = mid;
      this.replyModal = true;
    },
    // 
    singleDelMessage(mid) {
      this.$Modal.confirm({
        title: "",
        content: "<p></p>",
        onOk: () => {
          this.batchDelMessage([mid]);
        }
      });
    },
    // 
    batchDelMessage(mids) {
      messageDel(mids).then(data => {
        if (data.c === 0) {
          this.loadMessageList(1);
          this.loadMessageCount();
        }
      });
    },
    cancel() {}
  }
};
</script>

<style lang="css">
.article-search {
  position: relative;
  margin: 30px;
  padding-right: 86.9px;
  height: 41.2px;
  overflow: hidden;
}

.search-box {
  width: 100%;
  height: 100%;
}

#article-search-btn {
  flex: 5;
  position: absolute;
  vertical-align: top;
  right: 0;
  top: 0;
}
</style>



